<template>
    <article class="p-2">
        <Crop
            ref="crop"
            :overlay="overlay"
            :width="width"
            :height="height"
            :img="img"
            :point.sync="point"
            :offset.sync="offset"
            :org="org"
            :scale="scale"
            :angle="angle"
            class="mt-2"
            style="border:1px solid #111;"
        />
        <ButtonLoadFile class="a-button a-button--primary" @loaded="onImageLoaded"></ButtonLoadFile>


        <section>
            <button class="a-button" @click="toggle"><i class="ion-md-add font-1"></i></button>
            <button class="a-button" @click="scale+=0.1"><i class="ion-md-add font-1"></i></button>
            <button class="a-button" @click="scale-=0.1"><i class="ion-md-remove font-1"></i></button>
            <button class="a-button" @click="angle-=10"><i class="ion-md-undo font-1"></i></button>
            <button class="a-button" @click="angle+=10"><i class="ion-md-redo font-1"></i></button>
        </section>


        <section>
            <button class="a-button a-button--success" @click="toggle">切换</button>

            <hr class="mt-2" />
            <!-- Offset -->
            <label class="a-input">
                <input v-model.number="offset[0]" type="range" />
                OffsetX: {{offset[0]}}
            </label>
            <label class="a-input">
                <input v-model.number="offset[1]" type="range" />
                OffsetY: {{offset[1]}}
            </label>
            <hr class="mt-2" />

            <label class="a-input">
                <input v-model.number="org[0]" type="range" />
                OX:{{org[0]}}
            </label>
            <label class="a-input">
                <input v-model.number="org[1]" type="range" />
                OY:{{org[1]}}
            </label>

            <label class="a-input">
                <input v-model.number="scale" min="0.1" max="2" step="0.1" type="range" />
                Scale: {{scale}}
            </label>
            <label class="a-input">
                <input v-model.number="angle" type="range" max="360" />
                A:{{angle}}
            </label>
        </section>
    </article>
</template>

<script>
import AnyTouch from '../../../packages/any-touch/dist/any-touch.umd';
import ButtonLoadFile from './ButtonLoadFile';
import Crop from './Crop';
import loadImage from './loadImage';
export default {
    name: 'DIY',

    components: { ButtonLoadFile, Crop },

    data() {
        return {
            context: null,
            img: null,
            org: [1432/2, 2216/2],
            angle: 0,
            point: [0, 0],
            scale: 1,
            offset: [0, 0],
            width: 1432,
            height: 2216,
            overlay: [],
            overlay0: [
                'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/iPhone.png?v=1596445086',
                'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/b059942a7321575b31567fab557b7626.png?v=1596445098',
            ],
            overlay1: [
                'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/01aee27d-3488-4e2f-9a8f-97a96928016d.png?v=1596503919',
            ],
        };
    },

    computed: {
        moveRate() {
            return this.$refs.crop.$el?.offsetWidth / this.width;
        },
    },

    mounted() {
        const at = new AnyTouch(this.$refs.crop.$el);
        at.on('panmove', this.onPanmove.bind(this));
        at.on('pinchstart', this.changeOrg.bind(this));
        at.on('rotatestart', this.changeOrg.bind(this));
        at.on('pinchmove', this.onPinchmove.bind(this));
        at.on('rotatemove', this.onRotatemove.bind(this));
        at.on('tap', this.changeOrg.bind(this));
        this.overlay = this.overlay0;
    },

    methods: {
        toggle() {
            // console.log(this.overlay);
            this.overlay = this.overlay.length == 0 ? this.overlay0 : this.overlay1;
        },
        onPanmove({ deltaX, deltaY }) {
            const [offsetX, offsetY] = this.offset;
            this.offset = [offsetX + deltaX / this.moveRate, offsetY + deltaY / this.moveRate];
        },

        onRotatemove({ deltaAngle }) {
            this.angle += deltaAngle;
        },

        onPinchmove({ deltaScale }) {
            this.scale = Math.round(this.scale * deltaScale * 100) / 100;
        },

        onImageLoaded(e) {
            const { img } = e[0].source;
            this.img = img;
            // const rect = this.getCenterRect(img.width, img.height,this.width,this.height);
        },

        changeOrg({ x, y }) {
            const rect = this.$refs.crop.$el.getBoundingClientRect();
            const pointInEl = [(x - rect.left) / this.moveRate, (y - rect.top) / this.moveRate];
            this.org = pointInEl;
        },
    },
};
</script>

<style scope lang="scss">
label {
    display: block;
}
</style>