<template>
    <canvas style="width:100%;" :width="width" :height="height"></canvas>
</template>

<script>
import loadImage from './loadImage';

export default {
    name: 'Crop',

    props: {
        overlay: {
            type: Array,
            default: () => [],
        },

        width: {
            type: Number,
            default: 300,
        },
        height: {
            type: Number,
            default: 300,
        },
        img: {
            type: Image,
        },

        scale: {
            type: Number,
            default: 1,
        },

        org: {
            type: Array,
            default: () => [0, 0],
        },

        angle: {
            type: Number,
            default: 0,
        },

        // 平移距离
        offset: {
            type: Array,
            default: () => [0, 0],
        },
    },

    data() {
        return {
            context: null,
            // canvas元素左上角在canvas坐标系中的坐标
            offsetInOB: null,
            // 图片在canvas内部距离canvas元素左上角的距离
            offsetInOA: null,
            imgWidth: null,
            imgHeight: null,
        };
    },

    computed: {
        rad() {
            return (this.angle * Math.PI) / 180;
        },
    },

    watch: {
        img(img) {
            // 计算图片居中需要的数据
            const { top, left, width, height } = this.getCenterRectOffsetTopLeft(
                img.width,
                img.height,
                this.width,
                this.height
            );
            this.offsetInOA = [left, top];
            this.offsetInOB = this.switchToOB(this.offsetInOA);
            this.imgWidth = width;
            this.imgHeight = height;
            this.$emit('update:offset', [left, top]);
        },

        /**
         * 任何操作都会影响offset,
         * 所以render放在这里
         */
        offset: {
            deep: true,
            handler(offset) {
                // problem offset此时还是旋转/缩放前的值
                // 需要同步...
                this.offsetInOA = offset;
                this.offsetInOB = this.switchToOB(this.offsetInOA);
                this.render();
            },
        },

        scale() {
            this.offsetInOA = this.switchToOA(this.offsetInOB);
            this.$emit('update:offset', this.offsetInOA);
        },

        org() {
            // 图片不动,
            // OB内的offset变化
            this.offsetInOB = this.switchToOB(this.offsetInOA);
            this.render();
        },

        angle(angle) {
            this.offsetInOA = this.switchToOA(this.offsetInOB);
            this.$emit('update:offset', this.offsetInOA);
        },
    },

    mounted() {
        this.context = this.$el.getContext('2d');
    },

    methods: {
        render() {
            if (!this.img) return;

            const { context } = this;
            // 初始值
            const { rad } = this;
            context.clearRect(0, 0, this.width, this.height);
            this.drawChessboard();

            context.save();
            context.translate(this.org[0], this.org[1]);
            context.scale(this.scale, this.scale);
            context.rotate(rad);
            context.drawImage(
                this.img,
                0,
                0,
                this.img.width,
                this.img.height,
                this.offsetInOB[0],
                this.offsetInOB[1],
                this.imgWidth,
                this.imgHeight
            );
            context.fillStyle = '#d10';
            context.fillRect(-30 / this.scale, -30 / this.scale, 60 / this.scale, 60 / this.scale);

            this.drawOverlay();

            context.restore();
        },

        async drawOverlay() {
            for (const overlay of this.overlay) {
                const img = await loadImage(overlay);
                this.context.drawImage(img, 0, 0, this.width, this.height);
            }
        },

        /**
         * 获取图片在canvas元素中的定位信息
         * 让图片居中对齐
         */
        getCenterRectOffsetTopLeft(width, height, viewWidth, viewHeight) {
            // console.log({ viewWidth, viewHeight, width, height });
            let rate = [width / viewWidth, height / viewHeight];
            if (rate[0] > rate[1]) {
                const newHeight = Math.floor((viewWidth * height) / width);
                return {
                    width: viewWidth,
                    height: newHeight,
                    top: (viewHeight - newHeight) / 2,
                    left: 0,
                };
            } else {
                const newWidth = Math.floor((viewHeight * width) / height);
                return {
                    width: newWidth,
                    height: viewHeight,
                    top: 0,
                    left: (viewWidth - newWidth) / 2,
                };
            }
        },

        /**
         * 元素坐标系中的点在canvas坐标系中的坐标
         * @param {Array} 元素坐标系中的点
         */
        switchToOB([x, y]) {
            // 计算平移
            const dx = x - this.org[0];
            const dy = y - this.org[1];
            // 计算旋转/缩放
            const { rad } = this;
            return [
                (Math.cos(rad) * dx + Math.sin(rad) * dy) / this.scale,
                (-Math.sin(rad) * dx + Math.cos(rad) * dy) / this.scale,
            ];
        },

        /**
         * 形变坐标系内的坐标转换成标准坐标系中的坐标
         * @param {Array} 形变坐标系内的点
         */
        switchToOA([x, y]) {
            // 计算平移
            // 计算旋转/缩放
            const { rad } = this;
            return [
                (Math.cos(rad) * x - Math.sin(rad) * y) * this.scale + this.org[0],
                (Math.sin(rad) * x + Math.cos(rad) * y) * this.scale + this.org[1],
            ];
        },

        drawRect() {
            const { width, height } = this.img;
            this.context.save();
            this.context.fillStyle = 'rgba(0,0,0,0.6)';
            this.context.fillRect(0, 0, width, height);
            this.context.restore();
        },

        drawChessboard() {
            const context = this.context;
            const count = [24, Math.ceil((24 * this.height) / this.width)];
            const size = this.width / count[0];
            const colorReverse = { '#fff': '#ddd', '#ddd': '#fff' };
            context.save();
            let color = '#ddd';
            let rowStartColor = color;
            for (let i = 0; i <= count[0]; i++) {
                for (let j = 0; j < count[1]; j++) {
                    context.fillStyle = color;
                    const x = 0 + size * i;
                    const y = 0 + size * j;
                    context.fillRect(x, y, size, size);
                    color = colorReverse[color];
                }
                rowStartColor = colorReverse[rowStartColor];
                color = rowStartColor;
            }
            context.stroke();
            context.restore();
        },
    },
};
</script>

<style scope lang="scss">
.crop-panel {
    width: 100%;
    display: block;
    margin: auto;
}
</style>